*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background: black;
    font-family: 'Times New Roman', Times, serif;
}

.console-container{
    display: flex;
    flex-direction: column;

}

.console{
    padding: 1.5rem;
    margin-top: 3rem;
}

.console-input{
    width: 100%;
    padding: 1.5rem;
    outline: none;
    color: white;
    background: rgba(255,255,255, 0.05);
}

.console-input-log,
.console-output-log {
  padding: 0.5rem 1rem;
  color: white;
}

.console-output-log {
  color: orange;
  font-weight: bold;
}